/*///////////////////////
// Some UTILITY classes
///////////////////////*/

.u-text-center {
  text-align: center;
}

.u-text-justify {
  text-align: justify;
}

.u-text-right {
  text-align: right;
}

.u-margin-bottom-big {
  margin-bottom: 8rem;
}

.u-margin-bottom-big-1 {
  margin-bottom: 8rem;
  /* margin-left: 40%; */
}

.u-margin-bottom-medium {
  margin-bottom: 6rem;
}

.u-margin-bottom-small {
  margin-bottom: 2rem;
}

.u-text-white {
  color: var(--color-text-white);
}

.u-text-gold {
  color: var(--color-secondary);
}

.display-inherit {
  display: inherit;
}

.display-inline {
  display: inline;
}

@media screen and (max-width: 712px) {
  .u-margin-bottom-big-1 {
    margin-bottom: 8rem;
    /* margin-left: 40%; */
    margin-top: 8rem;
  }
}